.dot-container {
    display: table;
    position: fixed;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;
    z-index: 2;
}
.dot-list {
    display: inline-block;
    margin: 0;
    padding: 0;
    list-style: none;
    cursor: default;
}
.dot-list .active p {
    transform: scale3d(1.3, 1.3, 1.3);
}
.dot-list .active p:after {
    height: 100%;
}
.dot-list li {
    position: relative;
    display: block;
    margin-top: 25px;
    margin-bottom: 25px;
    width: 8px;
    height: 8px;
}
.dot-list li p {
    top: -16px;
    left: 0;
    width: 100%;
    height: 100%;
    outline: none;
    border-radius: 50%;

    cursor: pointer;
    position: absolute;
    overflow: hidden;
    background-color: transparent;
    box-shadow: inset 0 0 0 2px rgb(0, 0, 0);
    transition: all 0.3s ease;
    transform: scale3d(1, 1, 1);
}
.dot-list li p:after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 0;
    left: 0;
    width: 100%;

    box-shadow: 0 0 0 2px rgb(0, 0, 0);
    transition: height 0.3s ease;
}

.dot-list li p:after{
    background-color:rgb(0, 0, 0);
    box-shadow: 0 0 1px rgb(0, 0, 0);
}
.dot-list li p{
    box-shadow: inset 0 0 0 2px rgb(0, 0, 0);
}

.dot-list-dark li p{
    box-shadow: inset 0 0 0 2px rgb(207, 205, 202) !important;
}
.dot-list-dark li p:after{
    background-color:rgb(207, 205, 202) !important;
}


/* Hover styles */
@media (hover: hover) {
    .dot-list li:hover p:after{
        height: 100%;
    }
    .dot-list p:hover {
        transform: scale3d(1.3, 1.3, 1.3);
    }
}



.dot-list-dark .tooltip .tooltiptext{
    background-color: rgba(255, 255, 255, 0.1) !important;
}

.tooltip .tooltiptext {
    visibility: hidden;
    opacity: 0;
    width: 70px;
    color: white;
    text-align: center;
    border-radius: 70px;
    padding: 7px;
    /* Position the tooltip */
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    z-index: 1;
    top: -10px;
    right: 20px;
    transition: 0.1s;
    border-width: 25px;
    font-size: 12px;
}

@media (hover: hover) {
    .tooltip:hover .tooltiptext {
        visibility: visible;
        opacity: 1;
        cursor: pointer;
    }
}
/* HAMBUGER */
.hamburger-container {
    left: 5px;
    animation:wobble 1s ease-in-out;
}
@media (hover: hover) {
    .hamburger-container:hover{
        transform: scale(1.1);
        background-color: rgba(27, 25, 25, 0.5);
    }
}

.hamburger {
    top: 23px;
    left: 13px;
    width: 25px;
    height: 4px;
    background: rgb(73, 69, 69);
    position: absolute;
    box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
    transition: 0.5s;
    border-radius: 30%;
}
.hamburger:before { top: -8px; }
.hamburger:after { top: 8px; }
.hamburger:before,
.hamburger:after {
    content: '';
    position:absolute;
    width: 25px;
    height: 4px;
    border-radius: 30%;
    background: rgb(73, 69, 69);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition:0.5s;
}

.hamburger-grow{
    transform: scale(1.1);
    background-color: rgba(27, 25, 25, 0.5);
}

.hamburger-container.active .hamburger {
    background:rgba(0,0,0,0);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0);
} 
.hamburger-container.active .hamburger:before {
    top: 0;
    transform:rotate(45deg);
}
.hamburger-container.active .hamburger:after {
    top: 0;
    transform:rotate(135deg);
}  
